import React from 'react';
import Todo from './Todo';
import TodoForm from './TodoForm';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheckSquare } from '@fortawesome/free-solid-svg-icons';

class AgnosticTodoApp extends React.Component {

  state = {
    notes: []
  }

  //This will load the state stored in local storage first time you open the app.
  componentWillMount = () => {
    try {
      let notes = JSON.parse(localStorage.getItem('notes'));
      if (notes) {
        this.setState((state) => {
          return {
            notes: notes,
          }
        });
      }
    } catch{ }
  }

  //This will update the local storage each time state changes during the app runs
  componentDidUpdate = (prevProp, prevState) => {
    if (this.state.notes.length !== prevState.notes.length) {
      localStorage.setItem('notes', JSON.stringify(this.state.notes));
    }
  }


  handleAddNote = (note) => {
    if (note) {
      this.setState((state) => {
        return {
          notes: state.notes.concat({ note })
        }
      });
    };
  }

  handleEditNote = (e, {note}) => {
    e.persist();
    this.setState((prevState) => {
      let notes = prevState.notes;
      let index = notes.indexOf(note);
      notes[index].note = e.target.value;
      return {
        notes
      }
    },() => {
      //Call back function when state changes
      localStorage.setItem('notes', JSON.stringify(this.state.notes));
    })
  }

  handleDeleteNote = ({notePassed}) => {
    let confirm = window.confirm('Are you sure to want to delete this Todo?');

    if(confirm){
      this.setState((prevState) => {
        return{
          notes: prevState.notes.filter((note) => {
            return note.note !== notePassed.note;
          })
        }
      })
    }
  }

  render() {
    return (
      <div className='todo-app'>
        <div className='header'>
          <div className='container header__container'>
            <div className='header__logo-container'>
            <FontAwesomeIcon className='header__logo' icon={faCheckSquare} />
            </div>
            <h1 className='header__title'>Agnostic Todo App</h1>
          </div>
        </div>
        <TodoForm onSubmit={(note) => this.handleAddNote(note)} />
        <div className='container'>
          <div className='todo-container'>
            {this.state.notes.map((note, index) => {
              return <Todo key={index} note={note} handleEditNote={this.handleEditNote} handleDeleteNote={this.handleDeleteNote} />
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default AgnosticTodoApp;


/*
C - Create -- DONE
R - Read
U - Update
D - Delete
*/
