import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';

class Todo extends React.Component {
    state = {
        disabled: true,
        checked: false
    }

    textInput = React.createRef();

    enableInput = () => {
        this.setState((prevState)=> {
            return{
                disabled: !prevState.disabled
            }
        },() => {
            this.textInput.current.focus();
        });
    }

    keyPressed = (e) => {
        if(e.key === 'Enter'){
            this.enableInput();
            this.textInput.current.blur();
        }
    }

    moveCursorToEnd = (e) => {
        let temp_value = e.target.value;
        e.target.value = '';
        e.target.value = temp_value;
    }

    handleBlur = () => {
        this.setState(() => {
            return{
                disabled: true
            }
        })
    }

    handleTodoState = () => {
        this.setState((prevState) => {
            return{
                checked: !prevState.checked
            }
        })
    }

    render() {
        return (
            <div className='todo'>
                <input className='todo__checkbox' type='checkbox' checked={this.state.checked} onChange={this.handleTodoState} />
                <input
                    className='todo__input'
                    ref={this.textInput}
                    disabled={this.state.disabled}
                    onKeyPress={this.keyPressed}
                    onFocus={this.moveCursorToEnd}
                    onBlur={this.handleBlur}
                    type='text'
                    value={this.props.note.note}
                    onChange={(e) => { this.props.handleEditNote(e, { note: this.props.note }) }}
                />
                <div className='todo__icons'>
                    <div className='todo__icons-edit'>
                        <FontAwesomeIcon onClick={this.enableInput} icon={faEdit} />
                    </div>
                    <div className='todo__icons-delete'>
                        <FontAwesomeIcon onClick={() => this.props.handleDeleteNote({ notePassed: this.props.note })} icon={faTrash} />
                    </div>
                </div>

            </div>
        );
    }
}

export default Todo;