import React from 'react';



class TodoForm extends React.Component {

    state = {
        note: ''
    };

    handleInputChange = (e) => {
        let note = e.target.value;
        this.setState(() => {
            return {
                note: note
            }
        });
    }

    onSubmit = (e) => {
        e.preventDefault();
        let note = e.target[0].value;
        if (note) {
            this.props.onSubmit(note);
            this.setState(() => {
                return {
                    note: ''
                }
            })
        }

    }

    render() {
        return (
            <div className='container'>
                <form className='form' onSubmit={this.onSubmit}>
                    <input className='form__input' type='text' placeholder='Type your todo here...' name='note' value={this.state.note} onChange={this.handleInputChange} />
                    <div>
                        <button className='form__button'>Add Todo</button>
                    </div>
                </form>
            </div>
        );
    }
};

export default TodoForm;

